import React, { useEffect, useRef, useState } from 'react'
import Draggable from 'react-draggable';
import { useWindowSize } from 'react-use';
import { interval } from 'd3-timer';

import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'

import { ReactComponent as Recycle } from './recycle_1.svg'
import { ReactComponent as Trash } from './trash_1.svg'
import { responsive } from '../../../components/ThemeProvider/theme';

let animation
let answredAnimation
const thres = 0.25
const moveSpeed = 0.04

const Card = ({ no, disabled, onAnswer, answered, containerWidth }) => {
  const [xOffset, setXOffset] = useState(0)
  const isAnswered = useRef()
  const restorePosition = () => {
    animation = interval(() => {
      setXOffset(x => {
        const to = Math.max(Math.abs(x) - containerWidth * moveSpeed, 0) * Math.sign(x)
        if (to === 0) animation.stop()
        return to
      })
    })
  }
  const answeredPosition = () => {
    isAnswered.current = true
    answredAnimation = interval(() => {
      setXOffset(x => {
        const to = Math.min(Math.abs(x) + containerWidth * moveSpeed, containerWidth)
        if (to === containerWidth) answredAnimation.stop()
        return to * Math.sign(answered)
      })
    })
  }
  useEffect(() => {
    if (answered && !isAnswered.current) {
      answeredPosition()
    }
  }, [answered])
  const s = Math.sign(xOffset)
  const abs = Math.abs(xOffset)
  const t = containerWidth * thres
  return (
    <Draggable
      axis="x"
      position={{ x: xOffset, y: 0 }}
      onStart={() => {
        if (animation && typeof animation.stop === 'function') {
          animation.stop()
        }
      }}
      onDrag={(e, { x }) => {
        setXOffset(x)
      }}
      onStop={() => {
        if (abs < t) {
          restorePosition()
        } else if (!disabled) {
          onAnswer(s)
        }
      }}
      disabled={disabled}
    >
      <Box height="100%">
        <Flex
          position="relative"
          flexDirection="column"
          borderRadius="4em"
          border="0.25em solid"
          borderColor="gray.500"
          bg="white"
          transformOrigin="center bottom"
          height="100%"
          style={{ transform: `rotate(${xOffset / t * 15}deg)` }}
        >
          <Box flex="1" bg="gray.200" borderRadius="4em 4em 0 0" opacity={disabled ? 0.5 : 1} />
          <Text opacity={disabled ? 0.5 : 1} textAlign="center" fontSize={responsive('4em', '2em')} py="0.5em">{no}</Text>
          <Box.Absolute
            top="-5em"
            style={{
              [xOffset < 0 ? 'left' : 'right']: '33%',
              opacity: Math.min(Math.abs(xOffset) / (containerWidth * thres), 1),
            }}
          >
            <Box width={responsive('40em', '20em')}>
              {xOffset < 0 ? <Recycle /> : <Trash />}
            </Box>
          </Box.Absolute>
        </Flex>
      </Box>
    </Draggable>
  )
}

export default Card
