import React, { useCallback, useEffect, useState } from 'react'
import { timeout } from 'd3-timer'

import Fullpage from '../../components/FullpageHeight';
import Container from '../../components/Container';
import Box from '../../components/Box';

import useShowHeader from '../../contexts/header/useShowHeader';

import Progress from './Progress';
import Cards from './Cards';
import Controls from './Controls';

let ticker
let ansTime
const total = 15
const sec = 5
const GamePage = () => {
  useShowHeader()
  const [no, setNo] = useState(1)
  const [finish, setFinish] = useState()
  const [canAnswer, setCanAnswer] = useState(true)
  const [answers, setAnswers] = useState([])
  const [time, setTime] = useState(sec)
  const onAnswer = useCallback((i) => {
    setCanAnswer(false)
    setAnswers(ans => {
      const newAns = [...ans]
      newAns[no - 1] = { ans: i, duration: new Date() - ansTime }
      return newAns
    })
    if (ticker) {
      ticker.stop()
    }
    timeout(() => {
      if (no < total) {
        setTime(sec)
        setNo(no + 1)
      } else {
        setFinish(1)
      }
      setCanAnswer(true)
    }, 500)
  }, [no])
  const timeUp = () => {
    onAnswer(0)
  }
  const tick = () => {
    setTime((t) => {
      if (t - 1) {
        ticker = timeout(tick, 1000)
        return t - 1
      } else {
        timeUp()
        return 0
      }
    })
  }
  useEffect(() => {
    if (no <= total) {
      ansTime = new Date()
      ticker = timeout(tick, 1000)
    }
  }, [no, total])
  return (
    <Fullpage bg="primary" overflow="hidden">
      <Container px="2em" height="100%">
        {finish ? (
          <Box textAlign="center" my="2em" fontSize="6em">
            <Box>Finish!</Box>
            <Box>總耗時：{Math.round(answers.reduce((allT, a) => allT + a.duration / 1000, 0))}秒</Box>
          </Box>
        ) : (
          <Box.Relative height="100%">
            <Progress no={no} total={total} />
            <Cards
              no={no}
              total={total}
              answers={answers}
              onAnswer={onAnswer}
              disabled={!canAnswer}
            />
            <Controls onAnswer={onAnswer} disabled={!canAnswer} time={time} />
          </Box.Relative>
        )}
      </Container>
    </Fullpage>
  )
}

export default GamePage
