import React, { useContext, useMemo, useRef, useState } from 'react'
import { get, range } from 'lodash'
import Measure from 'react-measure'
import { useWindowSize } from 'react-use'

import containerContext from '../../../contexts/containerWidth/context'
import Box from '../../../components/Box'
import Card from './Card'
import { responsive } from '../../../components/ThemeProvider/theme'

const count = 3
const Cards = ({ no, total, disabled, onAnswer, answers }) => {
  const windowSize = useWindowSize()
  const { containerWidth } = useContext(containerContext)
  const max = useMemo(() => Math.min(no + count, total + 1), [no, total])
  return (
    <Box.Absolute
      top={responsive('37%', '45%')}
      left={0}
      right={0}
      transform="translateY(-50%)"
      pt="100%"
    >
      <Box.FullAbs p={responsive((containerWidth - windowSize.height / 2) / 2, '30%')}>
        <Box position="relative" width="100%" height="100%">
          {range(no, max).map((n, i) => (
            <Box
              position={i ? 'absolute' : 'relative'}
              key={n}
              transform={`scale(${1 - 0.05 * i}) translateY(${2 * i}%)`}
              transformOrigin="center bottom"
              zIndex={count - i + 1}
              width="100%"
              top="0"
              height="100%"
              left="0"
              right="0"
            >
              <Card
                no={n}
                disabled={disabled || i}
                onAnswer={onAnswer}
                answered={get(answers[n - 1], 'ans')}
                containerWidth={containerWidth}
              />
            </Box>
          ))}
        </Box>
      </Box.FullAbs>
    </Box.Absolute>

  )
}

export default Cards
